@import 'styles/colors.scss';

.App {
  width: 100%;
  min-height: 600px;
  display: flex;
  justify-content: center;
  padding: 20px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -moz-transition: all 0.4s;
  -ms-transition: all 0.4s;
  -o-transition: all 0.4s;
  z-index: -1;
}
